<template>
  <div :class="classes">
    <div :class="titleClasses"  @click="">{{blogItem.title}}</div>
    <div :class="imgClasses"  @click="">
      <img :src="blogItem.img" :alt="blogItem.title + '配图'">
    </div>
    <p :class="contentClasses">
      {{blogItem.content | stringSlice}}
    </p>
    <div :class="infoClasses">
      <a class="author">{{blogItem.author}}</a>|<span class="date">{{blogItem.date}}</span>
    </div>
  </div>
</template>

<script>
  const prefixCls = 'gtu-blog-item'

  export default {
    name: 'blog_item',
    props: {
      blogItem: {
        type: Object,
        required: true
      }
    },
    data () {
      return {}
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      titleClasses () {
        return [`${prefixCls}-title`]
      },
      imgClasses () {
        return [`${prefixCls}-img`]
      },
      contentClasses () {
        return [`${prefixCls}-content`]
      },
      infoClasses () {
        return [`${prefixCls}-info`]
      }
    },
    filters: {
      stringSlice (value) {
        // alert(document)
        return value
      },
      dateFormat (value) {
        return value
      }
    }
  }
</script>
<style lang="scss">
  $prefixCls : '.gtu-blog-item';

  #{$prefixCls} {
    padding: 40px 0 50px;
    border-bottom: 1px solid #eee;
    &-title {
      margin: 0 0 10px 0;
      cursor: pointer;
      font: {
        size: 30px;
        weight: normal;
      }
    }
    &-img {
      font-size: 0;
      cursor: pointer;
      img {
        max-width: 100%;
      }
    }
    &-content {
      margin: 15px 0;
      line-height: 1.8;
      font-size: 16px;
      text-align: justify;
    }
    &-info {
      color: #999;
      font-size: 14px;
      vertical-align: middle;
      .author {
        margin-right: 10px;
        color: #999;
        &:hover {
          text-decoration: underline;
        }          
      }
      .date {
        margin-left: 10px;
      }
    }
  }
</style>



