<template>
  <div>
    <BlogItem v-for="(blog, i) in blogList" :blogItem="blog" :key="i"></BlogItem>
    <Pagination :current="1" :total="10"></Pagination>
  </div>
</template>

<script>
  import {blogsImgDir} from '@/utils/staticUrl'

  export default {
    name: 'blog_list',
    components: {
      BlogItem: () => import(/* webpackChunkName:"blog" */'./BlogItem'),
      Pagination: () => import(/* webpackChunkName:"common" */'./Pagination')
    },
    data () {
      return {
        blogList: Array(5).fill(
          {
            title: '前几天让微信修改了启动页图的风云系列卫星什么来头',
            img: `${blogsImgDir}/image-2.png`,
            content: '9月25日，微信启动页的更换让朋友圈掀起了一场刷屏活动，消息称微信启动页的背景图将著名的蓝色弹珠替换为我国风云四号A星拍摄的一张卫星云图，那么这个风云系列的卫星到底什么来头？风云系列卫星我国最早在20世纪70年代开始发展我国的气象卫星，截止目前已经发射了14颗气象卫星',
            author: 'lanyan',
            date: '20170301'
          }
        )
      }
    },
    beforeCreate () {
      console.log((() => import(/* webpackChunkName:"blog" */'./BlogItem'))())
      /* this.$http.get('/users/lanyan', '').then(
        function (response) {
          console.log(response)
        },
        function (response) {
          console.log(response)
        }
      ) */
    }
  }
</script>


