<template>
  <div :class="classes">
    <Banner :class="bannerClasses" :height="200">
      <template slot="header">GISTOYOU BLOG</template>
      <template>解锁地理位置背后的价值</template>
    </Banner>
    <div :class="listWrapClasses">
      <BlogList></BlogList>
    </div>
  </div>
</template>

<script>
  const prefixCls = 'gtu-blog'

  export default {
    name: 'blog',
    components: {
      Banner: () => import(/* webpackChunkName:"common" */ '@/components/banner'),
      BlogList: () => import(/* webpackChunkName:"blog" */ './BlogList')
    },
    data () {
      return {
        list: []
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      bannerClasses () {
        return [`${prefixCls}-banner`]
      },
      listWrapClasses () {
        return [`${prefixCls}-list-wrap`]
      }
    }
  }
</script>
<style lang="scss">
  $prefixCls : '.gtu-blog';
  $wrapWidth: 760px;

  #{$prefixCls} {
    &-banner {
      display: flex;
      align-items: center;
      background-color: #F5F8FA;
      .gtu-banner-desc-header {
        color: #495060;
      }
      .gtu-banner-desc-content {
        color: #999;
      }
    }
    &-list-wrap {
      width: $wrapWidth;
      margin: 0 auto;
    }
  }
</style>


